@import url('https://fonts.googleapis.com/css2?family=Mukta:wght@300;400;500;600&family=Raleway:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  padding: 0;
  background-color: #0b0e1b;
  font-family: 'Mukta', sans-serif;
  font-family: 'Raleway', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p,
b {
  all: unset;
}